@import "./variables.scss";
h1 {
  margin: 0;
  padding: 0;
}
.footer-container {
  //   max-width: 100%;
  width: 100%;
  margin: 0 auto;
  background: $bg;
  padding: 4rem 0;
  box-shadow: 0rem 0.7rem 1rem 0.5rem $darkblue;
  display: flex;
  align-items: flex-start;
}
.footer-content {
  width: 100%;
  margin: 0 1rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.mobile-col-1 {
  width: 45%;
  justify-content: space-between;
  display: flex;
  align-items: flex-start;
}
.mobile-col-2 {
  width: 55%;
  display: flex;
  align-items: flex-start;
}
.footer-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.col {
  width: 100%;
  margin-right: 1rem;
}
img {
  width: 4rem;
}
.footer-item {
  margin: 0 0 3rem 0;
  display: flex;
  align-items: center;

  .footer-link {
    color: $darkblue;
    font-size: medium;
    text-transform: capitalize;
  }
  .heading {
    color: $darkblue;
    font-size: large;
    font-weight: 600;
  }
}
.footer-btn {
  padding: 1rem 2rem;
  border: none;
  color: #fff;
  border-radius: 0.6rem;
  background: $darkblue;
  transition: all 0.4s ease-in-out;
  &:hover {
    a {
      color: $darkblue;
    }
    background: #fff;
  }
  a {
    font-size: 2rem;
    color: #fff;
    transition: all 0.4s ease-in-out;
  }
}
.footer-para {
  color: $darkblue;
}
.footer-heading {
  color: $darkblue;
  font-size: 3rem;
}
.footer-icon {
  margin-right: 0.3rem;
  font-size: 1.6rem;
  color: $darkblue;
}
.heading-icon {
  font-size: 2.4rem;
  color: $darkblue;
  margin-right: 0.5rem;
}

//========================== Mobile Version =========================//
@media (max-width: 720px) {
  .col {
    margin: 0;
  }
  .mobile-col-1 {
    width: 100%;
    display: flex;
  }
  .mobile-col-2 {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 4rem;
  }
  .footer-content {
    display: flex;
    gap: 4rem;
    flex-direction: column;
  }
}
